body {
  margin: 0;
  padding: 0;

  

  header {
    &.header-top { @include span-columns(12); }
    &.header-middle-left { @include span-columns(6); }
    &.header-middle-right { @include span-columns(6); }
    &.header-bottom-left { @include span-columns(6); }
    &.header-bottom-right { @include span-columns(6); }
    
    //padding: 0 2%;
  }



  
  main {
    @include outer-container;
    //padding: 0 3%;

  }


  &.no-sidebars {
    main {
      section#content {
        @include span-columns(12);
      }
    }
  }

  &.one-sidebar.sidebar-first {
    main {
      aside {
        &#sidebar-first {
          @include span-columns(4);
          
        }
      }

      section#content {
        @include span-columns(8);
      }
    }
  }

  &.one-sidebar.sidebar-second {
    main {

      aside {
        &#sidebar-second {
          @include span-columns(4);

          @include media($tablet) {
              @include span-columns(4);


          }
        }
      }

      section#content {
        @include span-columns(8);
                
        @include media($tablet) {
          @include span-columns(4);

        }

      }
    }
  }

  &.two-sidebars {
    main {
      aside {
        &#sidebar-first {
          @include span-columns(3);
        }

        &#sidebar-second {
          @include span-columns(3);
        }
      }

      section#content {
        @include span-columns(6);
      }
    }
  }


 footer {
    &.footer-top-left { @include span-columns(6); }
    &.footer-top-right { @include span-columns(6); }
    &.footer-bottom { @include span-columns(12); }
    //padding: 0 2%;
  }


}

.inner-wrapper {
  @include outer-container;
  //padding: 0 3%;

}









